import moment from "moment";
import cloneDeep from 'lodash/cloneDeep';

export default class ChartDataMapper {
  static parseData({rawConfirmed, rawRecovered, rawDeaths, isTotal}:
					   { rawConfirmed: any, rawRecovered: any, rawDeaths: any, isTotal: boolean }) {
	// const data = rawConfirmed.map((d: any, i: number) => {
	//   const result = Object.assign({}, d);
	//   const isDiff: boolean = !isTotal && i > 0;
	//
	//   result.Confirmed = isDiff ? rawConfirmed[i].Cases - rawConfirmed[i - 1].Cases : rawConfirmed[i].Cases;
	//   result.Recovered = isDiff ? rawRecovered[i].Cases - rawRecovered[i - 1].Cases : rawRecovered[i].Cases;
	//   result.Deaths = isDiff ? rawDeaths[i].Cases - rawDeaths[i - 1].Cases : rawDeaths[i].Cases;
	//
	//   return result;
	// });
	//
	// const current = Object.assign({}, data[data.length - 1]);
	// const previous = Object.assign({}, data[data.length - 2]);
	//
	// // workaround to have the latest data shown
	// data.push({
	//   Date: moment().add(1, 'd').toString(),
	// });

	return isTotal
		? this.parseBarData({rawConfirmed, rawRecovered, rawDeaths})
		: this.parseLineData({rawConfirmed, rawRecovered, rawDeaths});

	// return this.parseCalendarData({rawConfirmed, rawRecovered, rawDeaths});
  }

  private static parseBarData({rawConfirmed, rawRecovered, rawDeaths}:
								  { rawConfirmed: any, rawRecovered: any, rawDeaths: any }) {
	const data = rawConfirmed.map((d: any, i: number) => {
	  const result = cloneDeep(d);

	  result.Confirmed = rawConfirmed[i].Cases;
	  result.Recovered = rawRecovered[i].Cases;
	  result.Deaths = rawDeaths[i].Cases;
	  result.Active = rawConfirmed[i].Cases - rawRecovered[i].Cases - rawDeaths[i].Cases;

	  return result;
	});

	const current = cloneDeep(data[data.length - 1]);
	const previous = cloneDeep(data[data.length - 2]);

	return {barData: data, current, previous};
  }

  private static parseCalendarData({rawConfirmed, rawRecovered, rawDeaths}:
									   { rawConfirmed: any, rawRecovered: any, rawDeaths: any }) {

	const data = rawConfirmed.map((rc: any, i: number) => (
			{
			  day: moment(rc.Date).format('YYYY-MM-DD'),
			  value: i > 0 ? rawConfirmed[i].Cases - rawConfirmed[i - 1].Cases : rawConfirmed[i].Cases,
			}
		)
	);

	return {calendarData: data, current: 0, previous: 0};
  }

  private static parseLineData({rawConfirmed, rawRecovered, rawDeaths}:
								   { rawConfirmed: any, rawRecovered: any, rawDeaths: any }) {
	const mappedConfirmed = rawConfirmed.map(this.mapper);
	const mappedRecovered = rawRecovered.map(this.mapper);
	const mappedDeaths = rawDeaths.map(this.mapper);

	const data: any = [{
	  id: 'confirmed',
	  data: mappedConfirmed,
	}, {
	  id: 'recovered',
	  data: mappedRecovered,
	}, {
	  id: 'deaths',
	  data: mappedDeaths,
	}];

	const current = cloneDeep(data[data.length - 1]);
	const previous = cloneDeep(data[data.length - 2]);

	return {lineData: data, current, previous};
  }

  private static mapper(d: any) {
	const isMobile = window.innerWidth < 750;

	return {
	  x: isMobile ? d.Cases : moment(d.Date).format('DD MMM'),
	  y: isMobile ? moment(d.Date).format('DD MMM') : d.Cases
	};
  }
}
