const darkGrey = '#333';
const textColor = '#c5c5c5';

const chartTheme = {
  axis: {
	ticks: {
	  line: {
		stroke: textColor,
	  },
	  text: {
		fill: textColor,
		fontFamily: 'Oswald',
	  },
	},
  },
  labels: {
	text: {
	  fontFamily: 'Oswald',
	  fontWeight: 400,
	}
  },
  legends: {
	text: {
	  textShadow: '1px 1px 1px #111',
	  fontFamily: 'Oswald'
	},
  },
  grid: {
	line: {
	  stroke: textColor,
	  strokeWidth: 1,
	  strokeDasharray: "5 5"
	}
  },
  tooltip: {
	container: {
	  background: darkGrey,
	  border: `1px solid ${textColor}`,
	  fontSize: 12,
	  textShadow: '1px 1px 1px #111',
	},
  },
};

export default chartTheme;
