import React from 'react';
import './App.css';
import Panel from "./components/Panel";
import {ThemeProvider} from "@material-ui/core";
import ChartService from "./services/ChartService";
import ChartDataMapper from "./utils/ChartDataMapper";
import darkTheme from "./utils/UiTheme";

export default class extends React.Component<any, any> {
  constructor(props: any) {
	super(props);

	this.state = {
	  barData: [],
	  lineData: [],
	  calendarData: [],
	  current: {},
	  previous: {},
	  rawConfirmed: [],
	  rawRecovered: [],
	  rawDeaths: [],
	  chartType: 'total',
	};
	this.onTypeChange = this.onTypeChange.bind(this);
  }

  componentDidCatch(error: any, errorInfo: any) {
	// You can also log the error to an error reporting service
	console.log(error);
  }

  componentWillMount() {
	this.getData();
  }

  parseData() {
	const isTotal = this.state.chartType === 'total';
	const chartData: any = ChartDataMapper.parseData({
	  rawConfirmed: this.state.rawConfirmed,
	  rawRecovered: this.state.rawRecovered,
	  rawDeaths: this.state.rawDeaths,
	  isTotal
	});

	this.setState({
	  ...chartData,
	});
  }

  onTypeChange(e: any) {
	this.setState({
	  chartType: e.target.value,
	});
	this.getData();
  }

  async getData() {
    const chartData: any = await ChartService.getData();

	this.setState(chartData);
	this.parseData();
  }

  render() {
	return (
		<ThemeProvider theme={darkTheme}>
		  <div className="App">
			<Panel
				   barData={this.state.barData}
				   lineData={this.state.lineData}
				   calendarData={this.state.calendarData}
				   current={this.state.current}
				   previous={this.state.previous}
				   onTypeChange={this.onTypeChange}
				   type={this.state.chartType}
			/>
			<div className={'footer'}>
			  By Andrei Bucin
			</div>
		  </div>
		</ThemeProvider>
	);
  }
}
