import React, {ReactText} from "react";
import moment from "moment";
import './Panel.css';
import {ResponsiveBar} from '@nivo/bar';
import {ResponsiveLine} from '@nivo/line';
import {ResponsiveCalendar} from '@nivo/calendar';
import Status from "./Status";
import MenuItem from "@material-ui/core/MenuItem";
import chartTheme from "../utils/ChartTheme";
import cloneDeep from 'lodash/cloneDeep';

const isMobile = window.innerWidth < 750;
const chartLayout = isMobile ? 'horizontal' : 'vertical';
const chartWidth = isMobile ? window.innerWidth : undefined;
const chartHeight = isMobile ? 800 : 600;
const chartMargins = isMobile
	? {top: 20, right: 10, bottom: 30, left: 40}
	: {top: 0, right: 30, bottom: 80, left: 60};

const red = '#ff7f7f';
const yellow = '#efdea4';
const green = '#35a8a8';
const darkGrey = '#333';
const textColor = '#c5c5c5';

/**
 * Formats chart date.
 * @param d
 */
const formatDate = (d: ReactText) => moment(d).format('DD MMM');
/**
 * Generates chart marker lines.
 * @param value
 * @param stroke
 * @param legend
 */
const generateMarker = (value: string, stroke: string, legend: string, strokeDasharray: string = ''): any => ({
  axis: isMobile ? 'y' : 'x',
  value,
  lineStyle: {stroke, strokeWidth: 1, strokeDasharray},
  legend,
  textStyle: {fill: textColor, fontSize: 12, textShadow: '1px 1px 1px #111'},
});
/**
 * Generates chart tooltip.
 * @param t
 */
const chartTooltip = (t: any) => {
  return (<div>
	<p>{formatDate(t.data.Date)}</p>
	<p style={{color: yellow}}>Active: {t.data.Active}</p>
	<p style={{color: red}}>Deaths: {t.data.Deaths}</p>
	<p style={{color: green}}>Recovered: {t.data.Recovered}</p>
  </div>);
};
/**
 * Computes bar label colors.
 * @param c
 */
const labelTextColor = (c: any) => ['Confirmed', 'Active'].includes(c.data.id) ? 'black' : 'white';
const axisBottom = isMobile ? {tickValues: 5} : {
  tickRotation: 45,
  format: formatDate,
};
const axisLeft = isMobile ? {
  tickRotation: 45,
  format: formatDate,
} : {tickValues: 5};

const typeOptions = [{
  id: 'total',
  label: 'Total',
}, {
  id: 'growth',
  label: 'Growth',
}];
const children = typeOptions.map((c, i) =>
	<MenuItem
		key={i}
		value={c.id}
	>
	  {c.label}
	</MenuItem>);

class Panel extends React.Component<any> {
  constructor(props: any) {
	super(props);
  }

  renderChart() {
	const barData = cloneDeep(this.props.barData);
	const lineData = cloneDeep(this.props.lineData);
	const calendarData = cloneDeep(this.props.calendarData);

	const calendarChart = <ResponsiveCalendar
		data={calendarData}
		from={calendarData[0] ? calendarData[0].day : new Date()}
		to={'2020-04-09'}
		colors={['#109176', '#0EA650', '#0CBB19',
		  '#41D109', '#96E805', '#FFFF00', '#FCDF03',
		  '#FABF05', '#F7A208', '#F5850A', '#F2690D',
		  '#F04F0F']}
		emptyColor="#c5c5c5"
		dayBorderColor={'#373737'}
		dayBorderWidth={2}
		monthBorderColor={'#373737'}
		tooltip={chartTooltip}
	/>
	const barChart = <ResponsiveBar
		data={barData}
		keys={['Deaths', 'Recovered', 'Active']}
		colors={[red, green, yellow]}
		indexBy={'Date'}
		layout={chartLayout}
		labelSkipHeight={isMobile ? undefined : 10}
		labelSkipWidth={!isMobile ? undefined : 20}
		padding={isMobile ? 0.15 : 0.1}
		enableGridX={isMobile}
		enableGridY={!isMobile}
		margin={chartMargins}
		theme={chartTheme}
		axisBottom={axisBottom}
		// @ts-ignore
		gridYValues={isMobile ? undefined : 5}
		// @ts-ignore
		gridXValues={isMobile ? 5 : undefined}
		axisLeft={axisLeft}
		axisTop={isMobile ? {tickValues: 5} : undefined}
		labelTextColor={labelTextColor}
		markers={[
		  generateMarker(isMobile ? '2020-03-03T00:00:00Z' : '2020-03-04T00:00:00Z', green, 'First recoveries'),
		  generateMarker(isMobile ? '2020-03-15T00:00:00Z' : '2020-03-16T00:00:00Z', yellow, 'Emergency state'),
		  generateMarker(isMobile ? '2020-03-21T00:00:00Z' : '2020-03-22T00:00:00Z', red, 'First deaths'),
		  generateMarker(isMobile ? '2020-03-24T00:00:00Z' : '2020-03-25T00:00:00Z', yellow, 'Full lockdown'),
		  generateMarker(isMobile ? '2020-03-29T00:00:00Z' : '2020-03-30T00:00:00Z', yellow, 'Emergency state (+2w)', '1 1'),
		  generateMarker(isMobile ? '2020-04-07T00:00:00Z' : '2020-04-08T00:00:00Z', yellow, 'Full lockdown (+2w)', '1 1'),
		]}
		tooltip={chartTooltip}
		legends={[
		  {
			itemTextColor: textColor,
			dataFrom: 'keys',
			anchor: 'bottom',
			direction: 'row',
			translateX: 0,
			translateY: 70,
			itemsSpacing: 0,
			itemWidth: 80,
			itemHeight: 20,
			itemDirection: 'left-to-right',
			itemOpacity: 0.85,
			symbolSize: 10,
			effects: [
			  {
				on: 'hover',
				style: {
				  itemOpacity: 1
				}
			  }
			]
		  }
		]}
	/>;
	const lineChart = <ResponsiveLine
		data={lineData}
		colors={[yellow, green, red]}
		enableGridX={isMobile}
		enableGridY={!isMobile}
		// @ts-ignore
		gridYValues={isMobile ? undefined : 5}
		// @ts-ignore
		gridXValues={isMobile ? 5 : undefined}
		// yScale={{
		//   type: "time",
		//   format: "%d %b",
		//   precision: "day"
		// }}
		margin={chartMargins}
		theme={chartTheme}
		curve={'monotoneX'}
		axisBottom={isMobile ? undefined : {tickRotation: 45}}
		axisLeft={isMobile ? {tickRotation: 45} : undefined}
		pointSize={isMobile ? 6 : 8}
		pointColor={darkGrey}
		pointBorderWidth={2}
		pointBorderColor={(d: any) => d.serieColor}
		markers={[
		  generateMarker(isMobile ? '03 Mar' : '04 Mar', green, 'First recoveries'),
		  generateMarker(isMobile ? '15 Mar' : '16 Mar', yellow, 'Emergency state'),
		  generateMarker(isMobile ? '21 Mar' : '22 Mar', red, 'First deaths'),
		  generateMarker(isMobile ? '24 Mar' : '25 Mar', yellow, 'Full lockdown'),
		  generateMarker(isMobile ? '29 Mar' : '30 Mar', yellow, 'Emergency state (+2w)', '1 1'),
		  generateMarker(isMobile ? '07 Apr' : '08 Apr', yellow, 'Full lockdown (+2w)', '1 1'),
		]}
		legends={[
		  {
			itemTextColor: textColor,
			anchor: 'bottom',
			direction: 'row',
			translateX: 0,
			translateY: 70,
			itemsSpacing: 0,
			itemWidth: 80,
			itemHeight: 20,
			itemDirection: 'left-to-right',
			itemOpacity: 0.85,
			symbolSize: 10,
			effects: [
			  {
				on: 'hover',
				style: {
				  itemOpacity: 1
				}
			  }
			]
		  }
		]}
	/>;

	const isTotal: boolean = this.props.type === 'total';

	return (<div className={'chart'} style={{height: chartHeight, width: chartWidth}}>
	  {!isTotal ? lineChart : barChart}
	  {/*{calendarChart}*/}
	</div>);
  }

  render() {
	const type = this.props.type;

	// @ts-ignore
	return (
		<div>
		  <h3>2020 Romanian COVID-19 cases</h3>
		  <div style={{margin: 5}}>
			{/*<FormControl style={{minWidth: isMobile ? '50%' : 120, textAlign: 'left'}}>*/}
			{/*  <InputLabel id="type-label">Summary type</InputLabel>*/}
			{/*  <Select labelId={'type-label'}*/}
			{/*		  value={type}*/}
			{/*		  children={children}*/}
			{/*		  onChange={this.props.onTypeChange} />*/}
			{/*</FormControl>*/}
			<Status
				text={'Latest confirmed'}
				currentCount={this.props.current.Confirmed}
				previousCount={this.props.previous.Confirmed}
			/>
			<Status
				text={'Latest deaths'}
				currentCount={this.props.current.Deaths}
				previousCount={this.props.previous.Deaths}
			/>
			<Status
				reverse={true}
				text={'Latest recovered'}
				currentCount={this.props.current.Recovered}
				previousCount={this.props.previous.Recovered}
			/>
		  </div>
		  {this.renderChart()}
		</div>
	);
  }
}

export default Panel;
